<template>
    <div class="block">
        <TitleWithButtons
            v-if="props.title"
            :title="props.title"
            :items="props.buttons"
        />

        <template v-if="props.type === 'list'">
            <div class="items-list">
                <ul class="items" v-if="data.items.length">
                    <li v-for="(item, id) in data.items"
                        :class="{ active: props.id && (props.id === item.code || props.id === item.id) }"
                        @click="onClick(item)"
                    >
                        <slot name="list-item" :item="item" />
                    </li>
                </ul>
                <ul class="items" v-if="props.skeleton && data.loading">
                    <li v-for="(item, id) in Array.from({ length: props.skeletonCount }, () => ({ code: Math.random() }))">
                        <slot name="list-skeleton"/>
                    </li>
                </ul>

                <Button :loading="data.loading"
                    class="tertiary size-l"
                    :name="t(`blocks.${props.code}.load`)"
                    v-if="props.loadMore && ((data.loading && !props.skeleton) || (data.items.length < data.total))"
                    @click="getItems"
                />

                <EmptyLabel
                    v-if="!data.items.length && !data.loading"
                    class="empty"
                    :title="props.empty?.title || t('common.empty.title')"
                    :description="props.empty?.description || t('common.empty.description')"
                />
            </div>
        </template>

        <template v-if="props.type === 'row'">
            <div class="slider-box">
                <Slider
                    v-if="data.items.length && !data.loading"
                    :colPreloadCount="Math.ceil(props.take / props.itemsInCol)"
                    :itemsInCol="props.itemsInCol"
                    :items="data.items"
                    v-bind="props.sliderProps"
                    ref="sliderRef"
                >
                    <template #default="{ item }">
                        <div class="slide" @click="onClick(item)">
                            <slot name="row-item" :item="item" />
                        </div>
                    </template>
                </Slider>

                <Slider
                    v-if="data.loading"
                    :colPreloadCount="Math.ceil(props.take / props.itemsInCol)"
                    :itemsInCol="props.itemsInCol"
                    :items="Array.from({ length: props.skeletonCount }, () => ({ code: Math.random() }))"
                    v-bind="props.sliderProps"
                    ref="sliderRef"
                >
                    <template #default="{ item }">
                        <div class="slide" @click="onClick(item)">
                            <slot name="row-skeleton" />
                        </div>
                    </template>
                </Slider>

                <EmptyLabel
                    v-if="!data.items.length && !data.loading"
                    class="empty"
                    :title="props.empty?.title || t('common.empty.title')"
                    :description="props.empty?.description || t('common.empty.description')"
                />
            </div>
        </template>
    </div>
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import { onMounted, reactive } from 'vue';
    import api from 'ðŸ”—/api';
    import TitleWithButtons from '@/components/ui/TitleWithButtons.vue';
    import Button from 'ðŸ”—/components/ui/Button.vue';
    import Slider from 'ðŸ”—/components/ui/Slider.vue';
    import EmptyLabel from 'ðŸ”—/components/ui/EmptyLabel.vue';
    import stores from '@/stores';

    const emit = defineEmits(['change']);

    const nav = stores.nav();


    const { t } = useI18n();

    const props = defineProps({
        code: { type: String, default: '' },
        empty: { type: Object, default: () => ({}) },
        items: { type: Array, default: () => ([]) },
        title: { type: [Object, String, null], default: null },
        params: { type: Object, default: () => ({}) },
        sort: { type: [Object, null], default: null },
        loadMore: { type: Boolean, default: true },
        skeleton: { type: Boolean, default: false },
        skeletonCount: { type: Number, default: 30 },
        take: { type: Number, default: 30 },
        itemsInCol: { type: Number, default: 1 },
        handler: { type: [String, null], default: () => null },
        type: { type: String, default: 'list' },
        source: { type: [Object, null], default: null },
        id: { type: [String, null], default: null },
        sliderProps: { type: Object, default: () => ({}) },
        buttons: { type: Array, default: () => ([]) },
        folder: { type: [Object, null], default: null },
        getById: { type: [String, null], default: null }
    });

    const data = reactive({
        items: props.items,
        loading: false,
        skip: props.items?.length || 0,
        total: props.items?.length || 0
    });


    const getState = () => ({
        items: data.items,
        total: data.total
    });

    const getItems = async () => {
        clearTimeout(data.setItemsTimeout);
        data.loading = true;

        if (!data.skip) {
            data.items = [];
        }

        let params;

        if (props.getById) {
            params = props.getById;
        } else {
            params = {
                ...(props.params || {}),
                skip: data.skip,
                take: props.take
            };

            if (props.sort) {
                params.sort = props.sort.code;
                params.sort_dir = props.sort.direction;
            }
        }

        const payload = await api[props.source.store]()[props.source.fnName](params) || [];

        data.setItemsTimeout = setTimeout(() => {
            if (payload?.items?.length) {
                data.items.splice(data.items.length, 0, ...(payload.items || []));
            }

            data.skip = data.items.length;
            data.total = payload?.total || 0;

            data.loading = false;
        }, 200);
    };

    const reload = () => {
        data.skip = 0;
        getItems();
    };

    const onClick = (item) => {
        emit('change', item);
    }

    defineExpose({
        reload,
        getState
    });

    onMounted(() => {
        if (props.source) {
            getItems();
        }
    });
</script>

<style lang="scss" scoped>
    @include default-items-list;


    .items-list {
        gap: 8px;

        .items {
            padding: 0;
            // padding: 16px 0;
            gap: 0;
            & > li {
                // padding-right: 0;
                padding-top: 4px;
                padding-bottom: 4px;

                &.active {
                    background: var(--light-20);
                }
            }
        }
    }

    .slider-box {
        width: 100%;
        overflow: hidden;

        .slider {
            padding: 0px 16px;
            scroll-padding-left: 16px;
            scroll-padding-right: 16px;
        }

        .loader {
            // min-height: 208px;
            margin: auto;
        }
    }

    .block {
        padding: 0 0 var(--block-gap);
        display: flex;
        flex-direction: column;
        gap: 32px;
        width: 100%;
        justify-content: start;


        .title {
            padding: 0 16px;
        }

        &.inline {
            .title {
                padding: 0;
            }

            .slider-box {
                .slider {
                    padding: 0; 
                }
            }
        }
    }


    .direction-rtl {
        .items-list {
            .items {
                & > li {
                    padding-right: 16px;
                    padding-left: 0;
                }
            }
        }
    }
</style>